.menu{
    width:100%
}
.menu-primary-enter{
    position:absolute;
    transform:translateX(-110%)
}
.menu-primary-enter-active{
    transform:translateX(0);
    transition:all var(--speed) ease;
}
.menu-primary-exit{
    position:absolute;
}
.menu-primary-exit-active{
    transform:translateX(-110%)
}